import React, { useState } from 'react';
import { Form, Button, Card  } from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';
import { Container } from "react-bootstrap";
import Swal from 'sweetalert2';


function Singup() {
/* Un gancho que te permite usar el estado en componentes funcionales. */
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [passwordConfirm, setPasswordConfirm] = useState('');
  const navigate = useNavigate();

/**
 * Si las variables password y passwordConfirm son iguales, el correo electrnico y la contrasea se
 * almacenan en localStorage y el usuario navega a la pgina de inicio. De lo contrario, se muestra un
 * mensaje de error.
 * @param event - El objeto de evento que se pasa al controlador de eventos.
 */
  const handleSubmit = (event) => {
 /* Evita que suceda la accin predeterminada del evento. */
  event.preventDefault();

  if (password === passwordConfirm) {
    localStorage.setItem('email', email);
    localStorage.setItem('password', password);

    navigate('/dashboard');
    
  }  else {
    Swal.fire({
      icon: 'error',
      title: 'Oops...',
      text: 'Las contraseas no coinciden. Por favor, intntelo de nuevo.'
    });
  }
}


/* Un componente React. formulario de registro */
  return (
    <>
    <Container className="d-flex aling-items-center justify-content-center" style={{ minHeight: "100vh" }}>
        <div className="w-100" style={{ maxWidth: "400px" }}>
      <Card className='mt-5'>
        <Card.Body>
          <h2 className="text-center mb-4">Registro</h2>
          <Form onSubmit={handleSubmit}>
            <Form.Group id="email">
              <Form.Label className="mt-2">Correo electrnico</Form.Label>
              <Form.Control
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </Form.Group>
            <Form.Group id="password">
              <Form.Label className="mt-2">Contrasea</Form.Label>
              <Form.Control
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </Form.Group>
            <Form.Group id="password-confirm">
              <Form.Label className="mt-2">Confirmar contrasea</Form.Label>
              <Form.Control
                type="password"
                value={passwordConfirm}
                onChange={(e) => setPasswordConfirm(e.target.value)}
                required
              />
            </Form.Group>
            <Button className="w-100 mt-4 btn btn-primary " type="submit">
              Registrarse
            </Button>
          </Form>
        </Card.Body>
      </Card>
    
      <div className="w-100 text-center mt-2"> 
        Ya tienes una cuenta? <Link to="/usuario">Iniciar Sesin</Link>
      </div>
      </div>
      </Container>

    </>
  );
}

export default Singup;
